let btnSearch = document.querySelector('.btn-search');
let searchForm = document.querySelector('.searchForm');
let inputFormSearch = searchForm.querySelector('input');

document.addEventListener('click', function(event) {
    const isClickedInsideSearch = btnSearch.contains(event.target) || searchForm.contains(event.target);
    event.stopPropagation();    
    if (!isClickedInsideSearch) {
        btnSearch.classList.remove('active');
        searchForm.classList.remove('active');
    }
});

btnSearch.addEventListener('click', function(event) {
    event.stopPropagation();
    btnSearch.setAttribute('type', 'submit');
    event.preventDefault();
    btnSearch.classList.add('active');
    searchForm.classList.add('active');
    inputFormSearch.focus();
});

searchForm.addEventListener('click', function(event) {
    
});

let btnHamburger = document.querySelector('.btnHamburger');
btnHamburger.addEventListener('click',function(){
    btnHamburger.classList.toggle('active')
});

window.onscroll = function() {scrollFunction()};

function scrollFunction() {
  var navbar = document.querySelector("nav");
  if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
    navbar.classList.add("scrolled");
  } else {
    navbar.classList.remove("scrolled");
  }
}
// Import modul React dan ReactDOM dari window.React dan window.ReactDOM
const { React, ReactDOM } = window;

// Definisikan komponen React
class MyReactComponent extends React.Component {
    render() {
        return (
            <div>
                <h1>Hi, this is a React component!</h1>
            </div>
        );
    }
}

// Lakukan mount komponen React di dalam file JavaScript
document.addEventListener('DOMContentLoaded', function() {
    const reactContainer = document.getElementById('reactComponent'); // Ambil elemen tempat komponen akan dirender

    if (reactContainer) {
        ReactDOM.render(<MyReactComponent />, reactContainer); // Lakukan render komponen React di dalam elemen tersebut
    }
});
